var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',[_c('b-container',{staticClass:"bv-example-row"},[_c('b-row',{staticClass:"mb-2"},[_c('b-col',[_c('b-card',{attrs:{"title":"SCOPE: Examining Technology-Enhanced Collaborative Care Management of Depression in the Cancer Setting","sub-title":"ACM Conference on Computer-Supported Cooperative Work and Social Computing. 2025."}},[_c('b-card-text',[_c('strong',[_vm._v("Anant Mittal")]),_vm._v(", Tae Jones, Jina Suh, Ravi Karkar, Spencer Williams, Yihao Zheng, Lydia M Andris, Ty W Lostutter, Amy M Bauer, Jesse R Fann, James Fogarty, and Gary Hsieh. ")]),_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.collapse-cscw-25",modifiers:{"collapse-cscw-25":true}}],staticClass:"card-link",staticStyle:{"background-color":"#7952b3"}},[_vm._v(" Abstract ")]),_c('b-button',{staticClass:"card-link",staticStyle:{"background-color":"#7952b3"},attrs:{"href":"https://doi.org/10.1145/3711060","target":"_blank"}},[_vm._v(" DOI "),_c('b-icon',{attrs:{"icon":"link"}})],1),_c('b-collapse',{staticClass:"mt-2",attrs:{"id":"collapse-cscw-25"}},[_c('p',{staticClass:"card-text"},[_vm._v(" Collaborative care management is an evidence-based approach to integrated psychosocial care for patients with comorbid cancer and depression. Prior work highlights challenges in patient-provider collaboration in navigating parallel cancer care and psychosocial care journeys of these patients. We design and deploy SCOPE, a platform for technology-enhanced collaborative care combining a patient-facing mobile app with a provider-facing registry. We examine SCOPE through a total of 45 interviews with patients and providers conducted in SCOPE's 15 months of design and development and 24 months of SCOPE's deployment for actual care in 6 cancer clinics. We find that: (1) SCOPE supported patient engagement in its underlying collaborative care and behavioral activation interventions, (2) patient-generated data in SCOPE improved patient-provider collaboration between and within in-person sessions, (3) SCOPE supported providers in delivering care and improved care team collaboration, (4) experience with SCOPE created evolving expectations for collaboration around data, and (5) SCOPE's deployment in actual care surfaced important implementation barriers. We discuss the implications of our findings in terms of designing for engagement with behavioral health interventions, negotiating patient data sharing and provider responsiveness, supporting personalized self-tracking goals in evidence-based interventions, exploring the role of digital health navigators in technology-enhanced care, and the need for flexibility in aligning technology-supported interventions to patient needs. ")])])],1)],1)],1),_c('b-row',{staticClass:"mb-2"},[_c('b-col',[_c('b-card',{attrs:{"title":"MigraineTracker: Examining Patient Experiences with Goal-Directed Self-Tracking for a Chronic Health Condition","sub-title":"ACM Conference on Human Factors in Computing Systems. 2024"}},[_c('b-card-text',[_vm._v(" Yasaman S Sefidgar, Carla L Castillo, Shaan Chopra, Liwei Jiang, Tae Jones, "),_c('strong',[_vm._v("Anant Mittal")]),_vm._v(", Hyeyoung Ryu, Jessica Schroeder, Allison Cole, Natalia Murinova, Sean A Munson, and James Fogarty. ")]),_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.collapse-chi-24",modifiers:{"collapse-chi-24":true}}],staticClass:"card-link",staticStyle:{"background-color":"#7952b3"}},[_vm._v(" Abstract ")]),_c('b-button',{staticClass:"card-link",staticStyle:{"background-color":"#7952b3"},attrs:{"href":"https://doi.org/10.1145/3613904.3642075","target":"_blank"}},[_vm._v(" DOI "),_c('b-icon',{attrs:{"icon":"link"}})],1),_c('b-collapse',{staticClass:"mt-2",attrs:{"id":"collapse-chi-24"}},[_c('p',{staticClass:"card-text"},[_vm._v(" Self-tracking and personal informatics offer important potential in chronic condition management, but such potential is often undermined by difficulty in aligning self-tracking tools to an individual's goals. Informed by prior proposals of goal-directed tracking, we designed and developed MigraineTracker, a prototype app that emphasizes explicit expression of goals for migraine-related selftracking. We then examined migraine patient experiences in a deployment study for an average of 12+ months, including a total of 50 interview sessions with 10 patients working with 3 different clinicians. Patients were able to express multiple types of goals, evolve their goals over time, align tracking to their goals, personalize their tracking, reflect in the context of their goals, and gain insights that enabled understanding, communication, and action. We discuss how these results highlight the importance of accounting for distinct and concurrent goals in personal informatics together with implications for the design of future goal-directed personal informatics tools. ")])])],1)],1)],1),_c('b-row',{staticClass:"mb-2"},[_c('b-col',[_c('b-card',{attrs:{"title":"Mobile App Use among Persons with Fibromyalgia: A Cross-Sectional Survey","sub-title":"Journal of Pain '24"}},[_c('b-card-text',[_vm._v(" Jiaxin An, Wei Fan, "),_c('strong',[_vm._v("Anant Mittal")]),_vm._v(", Yan Zhang, and Annie T. Chen. ")]),_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.collapse-jop-24",modifiers:{"collapse-jop-24":true}}],staticClass:"card-link",staticStyle:{"background-color":"#7952b3"}},[_vm._v(" Abstract ")]),_c('b-button',{staticClass:"card-link",staticStyle:{"background-color":"#7952b3"},attrs:{"href":"https://doi.org/10.1016/j.jpain.2024.03.011","target":"_blank"}},[_vm._v(" DOI "),_c('b-icon',{attrs:{"icon":"link"}})],1),_c('b-collapse',{staticClass:"mt-2",attrs:{"id":"collapse-jop-24"}},[_c('p',{staticClass:"card-text"},[_vm._v(" Persons with fibromyalgia experience a diverse set of symptoms. Recommendations for management generally focus on multidisciplinary approaches involving multiple modalities. Mobile apps can be an essential component for self-management, yet little is known about how persons with fibromyalgia use mobile apps for health-related purposes. A cross-sectional survey (N=663) was conducted to understand the real-world use of apps among persons with fibromyalgia. The survey included two main foci: 1) eHealth literacy and use of information sources, and 2) mobile app use patterns and preferences for health-related purposes, including the types of apps used and usage characteristics of apps that they used, as well as those that they had discontinued. Respondents’ average eHealth literacy as measured by eHEALS was 31.4 (SD=7.1), and they utilized diverse information sources. Approximately two-thirds of the sample used mobile apps; the remaining one-third did not. Diverse health management needs were represented in the apps reported, including scheduling/time management, notetaking, fitness, and wellness. Compared to apps that had been discontinued, participants rated apps that they still used higher in terms of ease of use and used them more frequently. Reasons for discontinuing app use included issues with privacy, the effort required, lack of interest, and lack of perceived quality. Other reasons for app non-use were lack of awareness and how-to knowledge, indicating that disseminating information about apps and addressing other barriers, such as providing user support, are critical to increasing uptake. These study findings can inform both app design and dissemination. ")])])],1)],1)],1),_c('b-row',{staticClass:"mb-2"},[_c('b-col',[_c('b-card',{attrs:{"title":"Jod: Examining Design and Implementation of a\n                            Videoconferencing Platform for Mixed Hearing\n                            Groups","sub-title":"ACM Conference on Computers and Accessibility. 2023"}},[_c('b-card-text',[_c('strong',[_vm._v("Anant Mittal")]),_vm._v(", Meghna Gupta, Roshni Poddar, Tarini Naik, SeethaLakshmi Kuppuraj, James Fogarty, Pratyush Kumar, and Mohit Jain. ")]),_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.collapse-assets-23",modifiers:{"collapse-assets-23":true}}],staticClass:"card-link",staticStyle:{"background-color":"#7952b3"}},[_vm._v(" Abstract ")]),_c('b-button',{staticClass:"card-link",staticStyle:{"background-color":"#7952b3"},attrs:{"href":"https://dl.acm.org/doi/abs/10.1145/3597638.3608382","target":"_blank"}},[_vm._v(" ACM "),_c('b-icon',{attrs:{"icon":"file-pdf-fill"}})],1),_c('b-collapse',{staticClass:"mt-2",attrs:{"id":"collapse-assets-23"}},[_c('p',{staticClass:"card-text"},[_vm._v(" Videoconferencing usage has surged in recent years, but current platforms present significant accessibility barriers for the 430 million d/Deaf or hard of hearing people worldwide. Informed by prior work examining accessibility barriers in current videoconferencing platforms, we designed and developed "),_c('em',[_vm._v("Jod")]),_vm._v(", a videoconferencing platform to facilitate communication in mixed hearing groups. Key features include support for customizing visual layouts and a notification system to request attention and influence behavior. Using "),_c('em',[_vm._v("Jod")]),_vm._v(", we conducted six mixed hearing group sessions with 34 participants, including 18 d/Deaf or hard of hearing participants, 10 hearing participants, and 6 sign language interpreters. We found participants engaged in visual layout rearrangements based on their hearing ability and dynamically adapted to the changing group communication context, and that notifications were useful but raised a need for designs to cause fewer interruptions. We provide insights for future videoconferencing designs and conclude with recommendations for conducting mixed hearing studies. ")])])],1)],1)],1),_c('b-row',{staticClass:"mb-2"},[_c('b-col',[_c('b-card',{attrs:{"title":"UW SCOPE: Examining Technology-Enhanced\n                            Collaborative Care Management of Depression in the\n                            Cancer Setting","sub-title":"CHI '23 Workshop on Bridging HCI and Implementation\n                        Science"}},[_c('b-card-text',[_vm._v(" Tae Jones*, "),_c('strong',[_vm._v("Anant Mittal*")]),_vm._v(", Jina Suh, Ravi Karkar, Lydia Andris, Yihao Zheng, Amy M. Bauer, Ty Lostutter, Jesse Fann, James Fogarty, and Gary Hsieh. ")]),_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.collapse-chi-23-workshop",modifiers:{"collapse-chi-23-workshop":true}}],staticClass:"card-link",staticStyle:{"background-color":"#7952b3"}},[_vm._v(" Abstract ")]),_c('b-button',{staticClass:"card-link",staticStyle:{"background-color":"#7952b3"},attrs:{"href":"https://homes.cs.washington.edu/~anmittal/UWSCOPE_CHI_2023_Workshop.pdf","target":"_blank"}},[_vm._v(" PDF "),_c('b-icon',{attrs:{"icon":"file-pdf-fill"}})],1),_c('b-collapse',{staticClass:"mt-2",attrs:{"id":"collapse-chi-23-workshop"}},[_c('p',{staticClass:"card-text"},[_vm._v(" The UW SCOPE study (Supporting Collaborative Care to Optimize Psychosocial Engagement in the Cancer Setting) is designing and developing a new web-based patient-provider platform for technology-enhanced Collaborative Care Management of depression in urban and rural cancer centers. The platform is currently deployed as part of a pragmatic effectiveness-implementation trial of technology-enhanced Collaborative Care vs. Usual Collaborative Care. This position paper for the CHI 2023 Workshop on Bridging HCI and Implementation Science introduces our setting of patients with cancer and depression, reviews our human-centered design of SCOPE, and reflects on our experiences with SCOPE as a case study at the intersection of human-centered design and implementation science. ")])])],1)],1)],1),_c('b-row',{staticClass:"mb-2"},[_c('b-col',[_c('b-card',{attrs:{"title":"How Data Scientists Use Computational Notebooks for Real-Time Collaboration","sub-title":"ACM Conference on Computer-Supported Cooperative Work and Social Computing. 2019"}},[_c('b-card-text',[_vm._v(" April Yi Wang, "),_c('strong',[_vm._v("Anant Mittal")]),_vm._v(", Christopher Brooks, and Steve Oney. ")]),_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.collapse-cscw-19",modifiers:{"collapse-cscw-19":true}}],staticClass:"card-link",staticStyle:{"background-color":"#7952b3"}},[_vm._v(" Abstract ")]),_c('b-button',{staticClass:"card-link",staticStyle:{"background-color":"#7952b3"},attrs:{"href":"https://dl.acm.org/doi/10.1145/3359141","target":"_blank"}},[_vm._v(" ACM "),_c('b-icon',{attrs:{"icon":"file-pdf-fill"}})],1),_c('b-collapse',{staticClass:"mt-2",attrs:{"id":"collapse-cscw-19"}},[_c('p',{staticClass:"card-text"},[_vm._v(" Effective collaboration in data science can leverage domain expertise from each team member and thus improve the quality and efficiency of the work. Computational notebooks give data scientists a convenient interactive solution for sharing and keeping track of the data exploration process through a combination of code, narrative text, visualizations, and other rich media. In this paper, we report how synchronous editing in computational notebooks changes the way data scientists work together compared to working on individual notebooks. We first conducted a formative survey with 195 data scientists to understand their past experience with collaboration in the context of data science. Next, we carried out an observational study of 24 data scientists working in pairs remotely to solve a typical data science predictive modeling problem, working on either notebooks supported by synchronous groupware or individual notebooks in a collaborative setting. The study showed that working on the synchronous notebooks improves collaboration by creating a shared context, encouraging more exploration, and reducing communication costs. However, the current synchronous editing features may lead to unbalanced participation and activity interference without strategic coordination. The synchronous notebooks may also amplify the tension between quick exploration and clear explanations. Building on these findings, we propose several design implications aimed at better supporting collaborative editing in computational notebooks, and thus improving efficiency in teamwork among data scientists. ")])])],1)],1)],1),_c('b-row',{staticClass:"mb-2"},[_c('b-col',[_c('b-card',{attrs:{"title":"Augmenting Authentic Data Science Environments for Learning Analytics","sub-title":"International Conference on Learning Analytics & Knowledge. 2019"}},[_c('b-card-text',[_c('strong',[_vm._v("Anant Mittal")]),_vm._v(", Christopher Brooks ")]),_c('b-button',{directives:[{name:"b-toggle",rawName:"v-b-toggle.collapse-lak-19",modifiers:{"collapse-lak-19":true}}],staticClass:"card-link",staticStyle:{"background-color":"#7952b3"}},[_vm._v(" Abstract ")]),_c('b-button',{staticClass:"card-link",staticStyle:{"background-color":"#7952b3"},attrs:{"href":"https://homes.cs.washington.edu/~anmittal/LAK19_Augmenting_Authentic_Data_Science_Environments_for_Learning_Analytics_Camera_Ready.pdf","target":"_blank"}},[_vm._v(" PDF "),_c('b-icon',{attrs:{"icon":"file-pdf-fill"}})],1),_c('b-collapse',{staticClass:"mt-2",attrs:{"id":"collapse-lak-19"}},[_c('p',{staticClass:"card-text"},[_vm._v(" Unlike general learning management systems which have used fine-grained trace behaviours to understand learning processes, data science environments use discipline-specific tools such as Project Jupyter (Perez and Granger, 2015). Augmentation of these tools is necessary in order to surface learner activities in ways which might be used for adaptation (Ferguson, 2012). This is analogous to augmenting problem-solving environments for mathematics (Melis and Siekmann, 2004), where domain-specific tools are necessary for understanding learning activity. In this work, we specifically tackle the augmentation of Project Jupyter. We explain the architecture of the environment along with the types of events we are able to collect and frame research questions we aim to answer with this work. ")])])],1)],1)],1)],1)],1)
